<template>
  <div id="app" class="text-white" style="background-color: black;">
    <transition enter-active-class="animated slideInDown" leave-active-class="animated slideOutUp"
                appear-active-class="animated slideInDown" mode="out-in" appear>
      <router-view></router-view>
    </transition>
    <app-navbar></app-navbar>
  </div>
</template>

<script>
  import Navbar from './components/Navbar.vue'

  export default {
    name: 'App',
    components: {
      appNavbar: Navbar
    }
  }
</script>

<style>
  #app {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    overflow-x: hidden;
  }
</style>



