<template>
  <div class="container"
       style="height: 85vh; overflow-y: scroll">
    <div class="row">
      <div class="col-md-6 order-md-1 order-2">
        <div class="col-md-12">
          <div class="row">
            <div class="col">
              <h5 class="text-center mt-2">Front End</h5>
            </div>
          </div>
          <div class="row">
            <div class="col-md-6">
              <p class="small mb-1">HTML</p>
              <div class="progress bg-light">
                <div class="progress-bar" role="progressbar" aria-valuenow="95" aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 95)">95%
                </div>
              </div>
              <p class="small mb-1">CSS</p>
              <div class="progress bg-light">
                <div class="progress-bar" role="progressbar" aria-valuenow="95" aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 95)">95%
                </div>
              </div>
              <p class="small mb-1">Javascript</p>
              <div class="progress bg-light">
                <div class="progress-bar" role="progressbar" aria-valuenow="90" aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 90)">90%
                </div>
              </div>
              <p class="small mb-1">Bootstrap</p>
              <div class="progress bg-light">
                <div class="progress-bar" role="progressbar" aria-valuenow="90" aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 90)">90%
                </div>
              </div>
              <p class="small mb-1">jQuery</p>
              <div class="progress bg-light">
                <div class="progress-bar" role="progressbar" aria-valuenow="95" aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 95)">95%
                </div>
              </div>
            </div>
            <div class="col-md-6">
              <p class="small mb-1">Gulp</p>
              <div class="progress bg-light">
                <div class="progress-bar" role="progressbar" aria-valuenow="79" aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 79)">
                  79%
                </div>
              </div>
              <p class="small mb-1">Webpack</p>
              <div class="progress bg-light">
                <div class="progress-bar" role="progressbar" aria-valuenow="89" aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 89)">
                  89%
                </div>
              </div>
              <p class="small mb-1">Vue.js</p>
              <div class="progress bg-light">
                <div class="progress-bar" role="progressbar" aria-valuenow="90" aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 90)">
                  90%
                </div>
              </div>
              <p class="small mb-1">Node.js</p>
              <div class="progress bg-light">
                <div class="progress-bar" role="progressbar" aria-valuenow="75" aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 75)">
                  75%
                </div>
              </div>
              <p class="small mb-1">Chart.js</p>
              <div class="progress bg-light">
                <div class="progress-bar" role="progressbar" style="0%;" aria-valuenow="90" aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 90)">90%
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-md-12 mr-auto">
          <div class="row">
            <div class="col">
              <h5 class="text-center mt-2">Back End</h5>
            </div>
          </div>
          <div class="row">
            <div class="col-md-6">
              <p class="small mb-1">PHP</p>
              <div class="progress bg-light">
                <div class="progress-bar bg-success" role="progressbar" aria-valuenow="95"
                     aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 95)">95%
                </div>
              </div>
              <p class="small mb-1">MySQL</p>
              <div class="progress bg-light bg-light">
                <div class="progress-bar bg-success" role="progressbar" aria-valuenow="97%"
                     aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 97)">
                  97%
                </div>
              </div>
              <p class="small mb-1">MongoDb</p>
              <div class="progress bg-light">
                <div class="progress-bar bg-success" role="progressbar" aria-valuenow="88"
                     aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 89)">
                  88%
                </div>
              </div>
              <p class="small mb-1">CodeIgniter</p>
              <div class="progress bg-light">
                <div class="progress-bar bg-success" role="progressbar" aria-valuenow="96"
                     aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 96)">
                  96%
                </div>
              </div>
            </div>
            <div class="col-md-6">
              <p class="small mb-1">Firebase</p>
              <div class="progress bg-light">
                <div class="progress-bar bg-success" role="progressbar" aria-valuenow="78"
                     aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 79)">
                  78%
                </div>
              </div>
              <p class="small mb-1">Node Express</p>
              <div class="progress bg-light">
                <div class="progress-bar bg-success" role="progressbar" aria-valuenow="65"
                     aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 65)">
                  65%
                </div>
              </div>
              <p class="small mb-1">Node REST API</p>
              <div class="progress bg-light">
                <div class="progress-bar bg-success" role="progressbar" aria-valuenow="80"
                     aria-valuemin="0"
                     aria-valuemax="100"
                     v-observe-visibility="(isVisible, entry) => visibilityChanged(isVisible, entry, 80)">
                  80%
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-6 order-md-2 order-1 d-flex justify-content-center align-items-center">
        <div class="col">
          <div id="skill-chart">
            <div id="myCanvasContainer" class="text-center">
              <canvas id="myCanvas" height="300px" width="300px">
                <p>Anything in here will be replaced on browsers that support the canvas element</p>
              </canvas>
            </div>
            <div id="tags">
              <ul>
                <li><a>PHP</a></li>
                <li><a>Codeigniter</a></li>
                <li><a>Javascript</a></li>
                <li><a>Vue.js</a></li>
                <li><a>HTML</a></li>
                <li><a>CSS</a></li>
                <li><a>Node.js</a></li>
                <li><a>Gulp</a></li>
                <li><a>Webpack</a></li>
                <li><a>Bootstrap</a></li>
                <li><a>jQuery</a></li>
                <li><a>Chart.js</a></li>
                <li><a>MySQL</a></li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Skills',
    data() {
      return {
        html: 0,
        css: 10,
        javascript: 10,
        isVisible: false
      }
    },
    methods: {
      visibilityChanged(isVisible, entry, value) {
        setTimeout(function () {
          entry.target.style.width = value + '%';
        }, 1000)
      }
    },
    mounted() {
      $('progress-bar').width('0%');
      if (!$('#myCanvas').tagcanvas({
          textColour: '#ffffff',
          outlineColour: 'none',
          reverse: true,
          depth: 0.8,
          maxSpeed: 0.05
        }, 'tags')) {
        // something went wrong, hide the canvas container
        $('#myCanvasContainer').hide();
      }
      $(function () {
        $('[data-toggle="tooltip"]').tooltip()
      })
    }
  }
</script>

<style>
  .progress {
    margin-bottom: 1rem;
  }

  .progress-bar {
    width: 0%;
  }

  /* width */
  ::-webkit-scrollbar {
    width: 5px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: grey;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: blue;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: darkblue;
  }

  /*#skill-chart {*/
  /*margin-top: 20%;*/
  /*}*/

  canvas#myCanvas {
    height: 400px;
    width: 400px;
  }

  @media (max-width: 576px) {
    /*#skill-chart {*/
    /*margin-top: 55rem;*/
    /*}*/
    canvas#myCanvas {
      height: 350px;
      width: 100%;
    }
  }

</style>



