<template>
  <div>
      <div class="text-center fixed-bottom">
        <router-link to="/" tag="button" class="my-2 mx-2 btn btn-outline-success border-0" data-toggle="tooltip" data-placement="top"
                title="Home">
          <icon class="m-1" name="home" scale="2"></icon>
        </router-link>
        <router-link to="/about" tag="button" class="my-2 mx-2 btn btn-outline-primary border-0" data-toggle="tooltip" data-placement="top" title="About">
          <icon class="m-1" name="user" scale="2"></icon>
        </router-link>
        <router-link to="/skills" tag="button" class="my-2 mx-2 btn btn-outline-danger border-0" data-toggle="tooltip" data-placement="top"
                title="Skills">
          <icon class="m-1" name="gear" scale="2"></icon>
        </router-link>
        <router-link to="/works" tag="button" class="my-2 mx-2 btn btn-outline-secondary border-0" data-toggle="tooltip" data-placement="top"
                title="Portfolios">
          <icon class="m-1" name="eye" scale="2"></icon>
        </router-link>
      </div>
  </div>
</template>

<script>
  export default {
    name: 'Navbar',
    data() {
      return {}
    },
    methods: {
      rounded() {
        $(event.target).animate({
          'border-radius': '3rem'
        }, 500, function () {
          // Animation complete.
        });
      },
      squared() {
        $(event.target).animate({
          'border-radius': '0'
        }, 500, function () {
          // Animation complete.
        });
      }
    },
    mounted() {
      $(function () {
        $('[data-toggle="tooltip"]').tooltip()
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



