<template>
    <div class="container d-flex justify-content-center flex-column align-items-center" style="height: 100vh;">
      <div class="h3">
        <vue-typer
          :text='["Hi, My Name is Buddy\nI am a Web Developer"]'
          :repeat='0'
          :shuffle='false'
          initial-action='typing'
          :pre-type-delay='1000'
          :type-delay='100'
          :erase-on-complete='false'
          caret-animation='blink'
        ></vue-typer>
      </div>
      <div class="my-2">
        <a href="mailto:buddyy93@gmail.com" class="btn btn-outline-primary">Contact Me</a>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'Home',
    data() {
      return {}
    },
    methods: {
      slideUp() {
        $(event.target).animate({
          'top': "-=20px",
          'border-radius': '3rem'
        }, 500, function () {
          // Animation complete.
        });
      },
      slideDown() {
        $(event.target).animate({
          'top': "+=20px",
          'border-radius': '0'
        }, 500, function () {
          // Animation complete.
        });
      }
    },
    mounted() {
//      if (!$('#myCanvas').tagcanvas({
//          textColour: '#ff0000',
//          outlineColour: '#ff00ff',
//          reverse: true,
//          depth: 0.8,
//          maxSpeed: 0.05
//        }, 'tags')) {
//        // something went wrong, hide the canvas container
//        $('#myCanvasContainer').hide();
//      }
      $(function () {
        $('[data-toggle="tooltip"]').tooltip()
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .vue-typer {
    font-family: monospace;
    color: white;

  }

  .vue-typer .custom.char {
    color: white;
  }

  .vue-typer .custom.char.selected {
    background-color: white;
  }

  .vue-typer .custom.caret {
    width: 1px;
    background-color: white;
  }
</style>



