// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App.vue'
import router from './router/index'
import VueTyperPlugin from 'vue-typer'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'animate.css/animate.min.css'
import 'lightgallery/dist/css/lightgallery.min.css'
import 'popper.js/dist/popper.min'
import 'bootstrap/dist/js/bootstrap.min'
import 'jquery-tagcanvas/jquery.tagcanvas.min'
import 'lightgallery/dist/js/lightgallery.min'
import 'lightgallery/modules/lg-thumbnail.min'
import 'lightgallery/modules/lg-fullscreen.min'
import 'vue-awesome/icons'
import Icon from 'vue-awesome/components/Icon'
import VueObserveVisibility from 'vue-observe-visibility'

Vue.use(VueObserveVisibility)
Vue.use(VueTyperPlugin)
Vue.component('icon', Icon)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  render: h => h(App)
});



